<template>
    <div id="alarm-list">
        <el-table :data="tableData" :header-cell-style="{background:'#eef1f6',color:'#606266',border:'1px solid #DFE1E4'}">
            <el-table-column prop="metric" label="监控指标"></el-table-column>
            <el-table-column label="阈值" header-align="center">
                <el-table-column prop="threshold.warning" label="提示"></el-table-column>
                <el-table-column prop="threshold.serious" label="严重"></el-table-column>
                <el-table-column prop="threshold.mortal" label="致命"></el-table-column>
            </el-table-column>
            <el-table-column prop="time_granularity" label="监控粒度"></el-table-column>
            <el-table-column prop="email" label="联系邮箱"></el-table-column>
        </el-table>
    </div>
</template>

<script>
import { alarmItem } from '../../request/api'
export default {
    data() {
        return {
            tableData: [],
        };
    },
    mounted() {
        this.initData()
    },
    methods: {
        async initData() {
            let res = await alarmItem()
            let data = res.data
            if (data.code != 200) {
                console.log(data.msg)
                return false
            }
            for (let i = 0; i < data.data.length; i++) {
                let { cpu_threshold, memory_threshold, time_granularity, email } = data.data[i]
                this.tableData.push(
                    {
                        metric: 'cpu使用率',
                        threshold: cpu_threshold,
                        time_granularity: time_granularity,
                        email: email
                    }
                )
                this.tableData.push(
                    {
                        metric: '内存使用率',
                        threshold: memory_threshold,
                        time_granularity: time_granularity,
                        email: email
                    }
                )
            }
        }
    }
};
</script>

<style lang="less" scoped>
#alarm-list {
    margin-top: 10px;
}
</style>