<template>
    <div id="setting-wrapper">
        <div class="configure-wrapper">
            <div class="label-group">cpu使用率阈值</div>
            <div class="input-group">
                <el-input v-model="configure.cpu_threshold.warning" placeholder='提示（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
                <div class="split"> ＜ </div>
                <el-input v-model="configure.cpu_threshold.serious" placeholder='严重（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
                <div class="split"> ＜ </div>
                <el-input v-model="configure.cpu_threshold.mortal" placeholder='致命（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
            </div>
        </div>
        <div class="configure-wrapper">
            <div class="label-group">内存使用率阈值</div>
            <div class="input-group">
                <el-input v-model="configure.memory_threshold.warning" placeholder='提示（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
                <div class="split"> ＜ </div>
                <el-input v-model="configure.memory_threshold.serious" placeholder='严重（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
                <div class="split"> ＜ </div>
                <el-input v-model="configure.memory_threshold.mortal" placeholder='致命（0-1）' style="width:100px" size="small" class="threshold-group"></el-input>
            </div>
        </div>
        <div class="configure-wrapper">
            <div class="label-group">监控粒度</div>
            <el-select class="input-group" v-model="configure.time_granularity" placeholder="请选择监控间隔" size="small" style="width:150px">
                <el-option v-for="item in granularity_options" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
        </div>
        <div class="configure-wrapper">
            <div class="label-group">联系邮箱</div>
            <el-input v-model="configure.email" placeholder="请输入告警联系邮箱，可填写多个，用分号分隔,如XXX@163.com;XXX@qq.com" style="width:550px" size="small"></el-input>
        </div>
        <div class="configure-wrapper">
            <div class="label-group"></div>
            <el-button type="primary" @click="addConfigure">添加配置</el-button>
            <el-button @click="resetConfigure">重置</el-button>
        </div>
    </div>
</template>

<script>
import {alarmSetting} from '../../request/api'
export default {
    data() {
        return {
            type: "",
            configure: {
                cpu_threshold: {
                    warning: "",
                    serious: "",
                    mortal: "",
                },
                memory_threshold: {
                    warning: "",
                    serious: "",
                    mortal: "",
                },
                time_granularity: "",
                email: '',
            },
            email_arr: [],
            granularity_options: [
                {
                    value: 1,
                    label: "1分钟",
                },
                {
                    value: 2,
                    label: "2分钟",
                },
                {
                    value: 5,
                    label: "5分钟",
                },
                {
                    value: 10,
                    label: "10分钟",
                },
            ],
        };
    },
    mounted() {
    },
    methods: {
        async addConfigure() {
            if (!this.threshold_verify(this.configure.cpu_threshold || !this.threshold_verify(this.configure.memory_threshold))) {
                return;
            } else if (this.configure.time_granularity == "") {
                this.$message.warning("请选择监控粒度");
            } else if (this.email_verify(this.configure.email)) {
                // 封装请求数据
                let data = {
                    cpu_threshold:this.configure.cpu_threshold,
                    memory_threshold:this.configure.memory_threshold,
                    time_granularity:this.configure.time_granularity,
                    email:this.email_arr
                }
                let res = await alarmSetting(data)
                if(res.data.code == 200){
                    this.$message.success("成功添加配置")
                }else{
                    this.$message.error(res.data.msg)
                }
            }
        },
        // 邮箱合法性判定
        email_verify(email) {
            if (email.length == 0) {
                this.$message.warning("联系邮箱不能为空")
                return false
            }
            let checkCn = /\uff1b/
            if (checkCn.test(email)) {
                email = email.replace(/\uff1b/g, ";")
            }
            let arr = email.split(";")
            let reg = /^([a-zA-Z0-9])[a-zA-Z0-9]+@[a-zA-Z0-9]+\.([a-zA-Z0-9]{2,5})$/
            for (let i = 0; i < arr.length; i++) {
                if (reg.test(arr[i])) {
                    console.log('true')
                } else {
                    this.$message.warning("存在不合法邮箱格式")
                    return false
                }
            }
            this.email_arr = arr
            return true
        },
        // 阈值合法性判定
        threshold_verify(threshold) {
            let { warning, serious, mortal } = threshold;
            if (warning == "" || serious == "" || mortal == "") {
                this.$message.warning("阈值设置不能为空");
                return false;
            } else if (warning < 0 || warning > 1 || serious < 0 || serious > 1 || mortal < 0 || mortal > 1) {
                this.$message.warning("阈值数值请设置在0-1之间");
                return false;
            } else if (
                warning > serious ||
                warning > mortal ||
                serious > mortal
            ) {
                this.$message.warning("阈值区间设置有误");
                return false;
            } else {
                return true;
            }
        },
        // 重置配置
        resetConfigure() {
            this.configure = {
                cpu_threshold: {
                    warning: "",
                    serious: "",
                    mortal: "",
                },
                memory_threshold: {
                    warning: "",
                    serious: "",
                    mortal: "",
                },
                time_granularity: "",
                email: "",
            };
        },
    },
};
</script>

<style lang="less" scoped>
@import "../../style/setting";
</style>